.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  width: 400px;
  gap: 20px;
}

.ct-info-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
}

.progress-bar {
  display: flex;
  flex-direction: row;
  padding: 0 50px;
  align-items: center;
  gap: 10px;

  & > input {
    flex-grow: 1;
    height: 5px;
    cursor: pointer;
  }
}

/* 动画容器样式 */
.animation-container {
  width: 400px;
  height: 400px;
  margin: 0 auto;
}

/* 播放速度下拉框样式 */
.speed-selector {
  padding: 2px;
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: white;
}

/* 暂停/播放按钮样式 */
.play-pause-btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
}
